import { Component, OnInit, Input, ViewChild, Output, EventEmitter } from '@angular/core';
import { EncounterData } from '../../models/encounter';
import { MatExpansionPanel } from '@angular/material';

@Component({
  selector: 'app-encounter-detail',
  templateUrl: './encounter-detail.component.html',
  styleUrls: ['./encounter-detail.component.scss']
})
export class EncounterDetailComponent implements OnInit {
    @Input() encounter: EncounterData;
    @ViewChild('expansionPanel') expansionPanel: MatExpansionPanel;
    @Output()
    deleted = new EventEmitter<number>();
    reportGenerated = new EventEmitter<number>();

    constructor() { }

    ngOnInit() {
    }

    delete() {
        this.deleted.emit(this.encounter.id);
    }

    generateReport() {
        this.reportGenerated.emit(this.encounter.id);
    }

    // Manually handle expanding so that our menu button doesn't expand the panel
    expandPanel(event: Event) {
        if (!event.srcElement.classList.contains('ignore-click')) {
            event.stopPropagation(); // Preventing event bubbling

            if (!this.expansionPanel.expanded) {
                this.expansionPanel.open();
                this.expansionPanel.expanded = true;
            } else {
                this.expansionPanel.close()
                this.expansionPanel.expanded = false;
            }
        }
    }
}
